.sliderContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2.5rem 0 2rem 0;
}
.slider {
  position: relative;
  width: 100%;
  max-width: 900px;
  height: 340px;
  border-radius: 2rem;
  overflow: hidden;
  box-shadow: 0 4px 24px rgba(166,140,182,0.10);
  background: #fff8f5;
  display: flex;
  align-items: center;
}
.slide {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  opacity: 0;
  transition: opacity 0.7s cubic-bezier(.4,0,.2,1);
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 3rem;
  z-index: 1;
}
.slide.active {
  opacity: 1;
  z-index: 2;
}
.slideContent {
  background: rgba(255, 255, 255, 0.85);
  border-radius: 1.2rem;
  padding: 2rem 2.5rem;
  box-shadow: 0 2px 12px rgba(166,140,182,0.08);
  max-width: 420px;
}
.slideContent h2 {
  font-size: 2rem;
  color: #000;
  font-family: 'Poppins', 'Open Sans', Arial, sans-serif;
  margin-bottom: 0.7rem;
  font-weight: 600;
}
.slideContent p {
  font-size: 1.1rem;
  color: #3B2C35;
  margin: 0;
}
.sliderDots {
  position: absolute;
  left: 50%;
  bottom: 1.2rem;
  transform: translateX(-50%);
  display: flex;
  grid-gap: 0.7rem;
  gap: 0.7rem;
  z-index: 3;
}
.dot {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #f6c177;
  opacity: 0.4;
  cursor: pointer;
  transition: opacity 0.2s, background 0.2s;
  border: 2px solid #fff8f5;
}
.dot.active {
  opacity: 1;
  background: #000;
}
@media (max-width: 700px) {
  .slider {
    height: 220px;
    border-radius: 1.2rem;
  }
  .slideContent {
    padding: 1.1rem 1.2rem;
    border-radius: 0.7rem;
    max-width: 260px;
  }
  .slideContent h2 {
    font-size: 1.1rem;
  }
  .slideContent p {
    font-size: 0.9rem;
  }
  .slide {
    padding-left: 1rem;
  }
}
.aboutSection {
  max-width: 1200px;
  margin: 2.5rem auto 0 auto;
  display: flex;
  background: #fff8f5;
  border-radius: 1.5rem;
  box-shadow: 0 4px 24px rgba(166,140,182,0.10);
  overflow: hidden;
  align-items: stretch;
  position: relative;
}
.aboutImage {
  flex: 1 1;
  min-width: 280px;
  max-width: 48%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f9e6e0;
}
.aboutImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.aboutContent {
  flex: 1 1;
  padding: 2.5rem 2.5rem 2.5rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.aboutContent h2 {
  font-size: 2.5rem;
  color: #000;
  font-family: 'Poppins', 'Open Sans', Arial, sans-serif;
  margin-bottom: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  z-index: 2;
}

.aboutContent h2::before {
  content: 'about';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 6rem;
  font-family: 'Dancing Script', cursive;
  font-weight: 400;
  color: rgba(246,137,119,0.15);
  z-index: 1;
  white-space: nowrap;
  text-transform: lowercase;
}

.aboutSection::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 150px;
  height: 150px;
  background: linear-gradient(135deg, rgba(246,137,119,0.1), rgba(246,200,119,0.1));
  border-radius: 0 50% 0 50%;
  z-index: 1;
}
.aboutContent p {
  font-size: 1.1rem;
  color: #3B2C35;
  margin-bottom: 1.5rem;
  line-height: 1.7;
  position: relative;
  z-index: 2;
}
.aboutBtn {
  background: #f68977;
  color: #fff;
  border: none;
  border-radius: 2rem;
  padding: 1rem 2.5rem;
  font-size: 1rem;
  font-family: 'Poppins', 'Open Sans', Arial, sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(246,137,119,0.25);
  width: -moz-fit-content;
  width: fit-content;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.aboutBtn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

.aboutBtn:hover::before {
  left: 100%;
}

.aboutBtn:hover {
  background: #d96a5a;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(246,137,119,0.35);
}
@media (max-width: 900px) {
  .aboutSection {
    flex-direction: column;
    border-radius: 1rem;
  }
  .aboutImage, .aboutContent {
    max-width: 100%;
    min-width: 0;
    padding: 0;
  }
  .aboutContent {
    padding: 1.5rem 1.2rem;
  }
  .aboutContent h2 {
