  object-fit: cover;
  border-radius: 50% 50% 40% 40% / 60% 60% 40% 40%;
  box-shadow: 0 4px 24px rgba(166,140,182,0.12);
  background: #fff;
}
@media (max-width: 1000px) {
  .hero {
    flex-direction: column;
    padding: 2rem 1.5rem;
    grid-gap: 2rem;
    gap: 2rem;
  }
  .heroImage img {
    width: 220px;
    height: 220px;
  }
}
@media (max-width: 600px) {
  .hero {
    padding: 1.2rem 0.5rem;
    margin: 1rem 0 1.5rem 0;
    border-radius: 1rem;
  }
  .hero h1 {
    font-size: 1.5rem;
  }
  .hero p {
    font-size: 1rem;
  }
} 
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #f9f9f9;
} 
